import React from "react";

interface InputFieldProps extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  error?: string;
  highlight?: boolean;
}

const InputField: React.FC<InputFieldProps> = ({
  label,
  error,
  highlight,
  disabled,
  className = "",
  ...rest
}) => {
  const inputClasses = `
    w-full
    px-4
    rounded-md
    transition
    duration-150
    outline-none
    bg-gray-900
    text-white
    placeholder-gray-400
    border
    ${error ? "border-red-500" : highlight ? "border-orange-400 shadow-[0_0_0_1px_orange]" : "border-white"}
    ${disabled ? "opacity-50 cursor-not-allowed" : "focus:border-blue-500 focus:shadow-sm"}
    ${className}
  `;

  return (
    <div className="w-full  bg-amber-950">
      {label && (
        <label className="block text-white mb-1 text-sm font-medium">
          {label}
        </label>
      )}
      <input
        {...rest}
        disabled={disabled}
        className={inputClasses}
        style={{
          height: "40px",
        }}
      />
      {error && <p className="text-red-500 text-sm mt-1">{error}</p>}
    </div>
  );
};

export default InputField;
